
.gifs-container {
  border-top: 1px solid black;
   }
.gifs-subcontainer {
 display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 2rem 0rem;
}

img {
  background: rgb(0, 0, 0);
  max-width: 12rem;
  border-radius: 15px;
}

picture {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.gifs-container h2{
  background-color: rgba(255, 255, 255, 0.741);
  border-radius: 15px;
  padding: 0.4rem;
}
