
.gifs-container {
border-top: 1px solid black;
 }

.gifs-subcontainer {
  display: grid;
   grid-template-columns: 1fr 1fr 1fr;
   margin: 1.5rem 0rem;
   gap: 1rem;
 }
 
 img {
   background: rgb(0, 0, 0);
   max-width: 12rem;
   border-radius: 15px;
 }
 
 picture {
   display: flex;
   flex-direction: column;
   align-items: center;
 }
 
 .gifs-container h2{
   background-color: rgba(255, 255, 255, 0.741);
   border-radius: 15px;
   padding: 0.4rem;
   font-size: 0.9rem;
 }
 
 @media screen and (max-width: 750px) {

  img {
    background: rgb(0, 0, 0);
    max-width: 8rem;
    border-radius: 15px;
  }
  .gifs-container h2{
    background-color: rgba(255, 255, 255, 0.741);
    border-radius: 15px;
    padding: 0.4rem;
    font-size: 0.8rem;
  }
}
 @media screen and (max-width: 478px) {

  img {
    background: rgb(0, 0, 0);
    max-width: 7rem;
    border-radius: 15px;
  }
  .gifs-container h2{
    background-color: rgba(255, 255, 255, 0.741);
    border-radius: 15px;
    padding: 0.4rem;
    font-size: 0.8rem;
  }
}
